.password-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.password-card {
  background: linear-gradient(135deg, #e0f7fa, #b2ebf2);
  border-radius: 16px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 320px;
  padding: 25px;
  text-align: center;
  transition: transform 0.2s ease;
}

.password-card:hover {
  transform: scale(1.00);
}

.password-card h2 {
  color: #00796b;
  margin-bottom: 20px;
}

.options {
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: left;
  margin-bottom: 20px;
}

.generate-btn {
  background-color: #00796b;
  color: white;
  border: none;
  padding: 20px 40px;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
}

.generate-btn:hover {
  background-color: #004d40;
}

/* Generated password section */
.generated-password {
  margin-top: 30px;
  text-align: center;
}

.generated-password h3 {
  color: #00695c;
  margin-bottom: 10px;
}

.password-display {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.password-display input {
  background-color: #e0f2f1;
  padding: 10px 15px;
  border-radius: 8px;
  font-family: monospace;
  font-size: 16px;
  color: #004d40;
  border: 1px solid #80deea;
  width: 250px;
  text-align: center;
}

.password-display button {
  background-color: #00796b;
  color: white;
  border: none;
  padding: 20px 25px;
  border-radius: 8px;
  cursor: pointer;
}

.password-display button:hover {
  background-color: #004d40;
}
