body, html, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  background: linear-gradient(200deg, #98d8ea, #6997cf); /* light blue gradient */
  font-family: Arial, sans-serif;
}
/* App.css */
.light-background, .dark-background {
  min-height: 100vh; /* full height */
  width: 100%;
  transition: background-color 0.3s ease;
}

.light-background {
  background-color: #f5f5f5; /* your default light background */
  transition: background-color 0.3s ease;
}

.dark-background {
  background: linear-gradient(200deg, #98d8ea, #6997cf); /* light blue gradient */
  transition: background-color 0.3s ease;
}
