.navbar {
  background-color: #222;
  padding: 15px;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: center; /* aligns items horizontally */
  gap: 40px; /* spacing between links */
  margin: 0;
  padding: 0;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

.navbar a:hover {
  color: #00bcd4;
}

.logout-button {
  padding: 5px 10px;
  cursor: pointer;
  border: none;
  background-color: #555;
  color: white;
  border-radius: 5px;
}

.logout-button:hover {
  background-color: #777;
}

/* Search bar container (right side) */
.navbar .search-container {
  display: flex;
  align-items: center;
  position: relative;
}

/* Search input field */
.navbar input[type="text"] {
  padding: 8px 12px;
  border-radius: 20px;
  border: none;
  outline: none;
  width: 180px;
  
  background-color: #fff;
  color: #333;
  font-size: 0.95rem;
}

/* Expand input when focused */

/* Search icon inside input */
.search-container {
  padding: 8px 12px;
  border-radius: 20px;
  border: none;
  outline: none;
  width: 180px;
  margin-bottom: 1rem;
  background-color: #fff;
  color: #333;
  font-size: 0.95rem;
}
.search-container:focus {
  width: 250px;
  background-color: #f8f8f8;
}

.toggle-button {
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #333;
  cursor: pointer;
  margin-left: 10px;
}

.toggle-button.on {
  background-color: green;
  color: white;
}

.toggle-button.off {
  background-color: red;
  color: white;
}


