.vault-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 40px;
}

.vault-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 360px; /* width of the card */
}

.vault-card {
  background: linear-gradient(135deg, #e0f7fa, #b2ebf2);
  border-radius: 12px;
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.2s ease;
}

.vault-card:hover {
  transform: scale(1.02);
}

.vault-card p {
  font-family: monospace;
  font-size: 16px;
  color: #004d40;
  margin: 0;
  word-break: break-all;
}

.vault-card input {
  width: 180px;
  padding: 5px 10px;
  border-radius: 8px;
  border: 1px solid #80deea;
  font-family: monospace;
  font-size: 16px;
}

.vault-buttons {
  display: flex;
  gap: 8px;
}

.vault-buttons button {
  padding: 5px 12px;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  font-weight: 500;
  transition: 0.2s ease;
}

.vault-buttons button.edit-btn {
  background-color: #00796b;
  color: white;
}

.vault-buttons button.edit-btn:hover {
  background-color: #004d40;
}

.vault-buttons button.delete-btn {
  background-color: #d32f2f;
  color: white;
}

.vault-buttons button.delete-btn:hover {
  background-color: #9a0007;
}
